@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
#contact-us{
    display: flex;
}

.contact-info{
    width: 40%;
    padding: 25px;
    background-color: rgb(189, 189, 189);
}

.contact-info h1{
    font-size: 2rem;
    padding: 8px 0px;
}

.contact-info div{
    font-size: 1.3rem;
}

/* Styling Contact form */
.contact-form-div{
    font-family: 'Poppins', sans-serif;
    margin: 20px;
    margin-bottom: 80px;
    margin-left: 40px;
    width: 60%;
    display: flex;
}

.form-input-div{
    display: flex;
    flex-direction: column-reverse;
}

#contact-form{
    width: 55%;
}

.form-input-div label{
    font-family: 'Poppins', sans-serif;
    font-size: 1em;
}

.form-input-div input, textarea{
    font-family: 'Poppins', sans-serif;
    font-size: 1em;
    padding: 8px;
    outline: none;
    border: 1.5px solid black;
}

.form-input-div input:focus,textarea:focus{
    border: 1.5px solid rgba(230, 165, 47, 0.788);
}

#contact-form-submit-bt{
    padding: 10px 18px;
    font-size: 1em;
    font-family: 'Poppins', sans-serif;
    margin: 8px 0px;
    cursor: pointer;
    color: white;
    border: none;
    border-radius: 2px;
    background-color: rgba(230, 150, 1, 0.9);
}

#contact-form-submit-bt:Hover{
    background-color: rgb(230, 150, 0);
}