@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@700&family=Quicksand:wght@500&display=swap');

.blog-container{
    height: 325px;
    width: 280px;
    /* border: 2px solid black; */
    box-shadow: 0px 0px 5px 0px black;
    margin: 10px;
    transition: 0.3s;
}

.blog-image, .blog-image img{
    width: 100%;
}
.blog-image img{
    object-fit: cover;
    height: 100%;
}

.blog-image{
    overflow: hidden;
    height: 55%;
}

.blog-description{
    text-overflow: ellipsis;
    overflow: hidden;
    display: -webkit-box;
   -webkit-line-clamp: 2;
   -webkit-box-orient: vertical;

}

.blog-info{
    font-family: 'Quicksand', sans-serif;
    font-size: 1.1em;
    padding: 8px;
}

.blog-title{
    font-family: 'Nunito', sans-serif;
    font-size: 1.5em;
    text-overflow: ellipsis;
    overflow: hidden;
    display: -webkit-box;
   -webkit-line-clamp: 1;
   -webkit-box-orient: vertical;
}


.blog-author-info{
    display: flex;
    white-space: nowrap;
}

.author-img{
    display: flex;
    justify-content: center;
}

.author-img img{
    border-radius: 50%;
    padding: 5px;
    padding-left: 0;
    height: 50px;
    
}
.blog-container:hover{
    cursor: pointer;
    box-shadow: 0px 2px 9px 1px black;
    transform: translateY(-8px);
}