@import url('https://fonts.googleapis.com/css2?family=Ubuntu&display=swap');
.popup-primary-container {
    transition: 300ms;
}

.popup-primary-container::before {
    content: "";
    height: 100vh;
    width: 100vw;
    background-color: black;
    position: fixed;
    opacity: 0.5;
    z-index: 10;
    top: 0;
}

.popup-secondary-container {
    font-family: 'Ubuntu', sans-serif;
    width: 35%;
    position: fixed;
    z-index: 10;
    padding: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 30px;
    left: 50%;
    top: 50%;
    transition: 100ms;
    transform: translate(-50%, -50%);
    animation: popup-message-animation 100ms linear;
}

#loading-icon {
    font-size: 3rem;
    animation-name: loading-animation;
    animation-duration: 1s;
    animation-delay: 0ms;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    display: block;
    
    height: fit-content;
}

@keyframes loading-animation {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

@keyframes popup-message-animation {
    from {
        transform: translate(-50%, -65%);
        opacity: 0;
    }
    to {
        transform: translate(-50%, -50%);
        opacity: 1;
    }
}

.popup-message-sign i {
    font-size: 13rem;
    padding: 8px;
}

.popup-message-sign {
    text-align: center;
}

.popup-close-button {
    position: absolute;
    right: 20px;
    top: 15px;
    cursor: pointer;
}

.popup-close-button i {
    font-size: 2.2rem;
}
