@import url('https://fonts.googleapis.com/css2?family=Varela+Round&display=swap');

footer{
    display: flex;
    align-items: center;
    background-color: rgba(35, 47, 56, 0.5);
    padding: 10px;
}

.Footer-title{
    font-size: 2rem;
    display: inline;
    font-family: 'Raleway', sans-serif;
    padding: 8px 15px;
}

.Footer-title + span{
    padding: 6px 10px;
    border-left: 2px solid grey;
}

.Footer-link{
    color: black;
    cursor: pointer;
    display: inline;
    font-size: 2rem;
    padding: 5px;
    margin-left: auto;
}