@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

.catogories-div-primary{
    display: grid;
    grid-template-columns: auto auto auto;
    justify-content: center;
    padding: 50px;
}

.catogories{
    position: relative;
    height: 130px;
    width: 275px;
    border-radius: 8px;
    margin: 18px;
    cursor: pointer;
    box-shadow: 0px 0px 5px 1px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    transition: 0.5s;
}

.catogories-image{
    width: 100%;
    height: 100%;
    opacity: 0.8;
    object-fit: cover;
    transition: 0.5s;
}

.catogories-title{
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
    font-size: 1.4rem;
    position: absolute;
    transition: 0.5s;
}

.catogories:hover .catogories-image{
    opacity: 1;
    transform: scale(1.2);
}