@import url('https://fonts.googleapis.com/css2?family=Radio+Canada&display=swap');

#about-me{
    display: flex;
    justify-content: center;
    align-items: center;
}

.about-primary-container{
    display: flex;
    background-color: rgb(189, 182, 182);
    width: 85%;
    margin: 50px;
}

.about-pic{
    width: 300px;
    height: 300px;
    min-width: 300px;
    padding: 20px;
    object-fit: contain;
}

.about-pic img{
    width: 100%;
    height: 100%;
}

.about-details{
    font-family: 'Radio Canada', sans-serif;
    padding: 20px 20px 20px 0px;
}

.about-name{
    font-size: 2em;

}

.about-desc{
    font-size: 1.2em;
}