@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@500&family=Varela+Round&display=swap');

/* Styling Navbar */
#navbar {
    position: fixed;
    width: 100%;
    top: 0;
    padding: 10px;
    display: flex;
    z-index: 1;
    align-items: center;
    justify-content: space-around;
    transition: 0.5s;
}

.Navbar-title {
    font-family: 'Raleway', sans-serif;
    font-size: xx-large;
    color: white;
    padding: 8px;
}

/* Styling Navigation bar */
.navigation {
    display: flex;
}

.navigation-tabs{
    opacity: 0.7;
    padding: 6px 12px;
    cursor: pointer;
    transition: 0.2s;
}
.navTabTitle {
    display: inline-block;
    font-family: "Open Sans", sans-serif;
    font-size: 1.1em;
    white-space: nowrap;
    color: white;
    font-weight: 500;
    text-decoration: none;
    transition: 0.2s;
}


.navigation-tabs:hover{
    opacity: 1;
}

.navigation-tabs:hover .navTabTitle,.navigation-tabs:hover .navTabBottom{
    transform: translateY(-3px);
}

.navigation-tabs:hover .navTabBottom div{
   max-width: 50%;
}

.navTabBottom{
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.2s;
}

.navTabBottom div{
    background-color: white;
    height: 2px;
    width: 50%;
    max-width: 0;
    transition: all 0.2s;
}

/* Styling User Button */
.nav-user-button {
    position: relative;
    display: flex;
    justify-content: center;
    margin: 5px;
}

#nav-user-bt-icon{
    opacity: 0.7;
    padding: 5px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border-radius: 10px;
    background-color: rgba(171, 209, 207, 0.616);
}

#nav-user-bt-icon i{
    font-size: 1.7rem;
}

.nav-user-button .tooltip {
    opacity: 0;
    position: absolute;
    background-color: white;
    border-radius: 10px;
    user-select: none;
    padding: 3px 15px;
    color: black;
    text-align: center;
    top: 90%;
    transition: 300ms;
    z-index: -1;
}

.nav-user-button .tooltip::before {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: transparent transparent white transparent;
}

#nav-user-bt-icon:hover + .tooltip {
    opacity: 0.7;
    top: calc(100% - -5px);
}

#nav-user-bt-icon:hover{
    opacity: 1;
}